$(document).ready(function () {
    const accessToken = localStorage.getItem('Authorization');
    const refreshToken = localStorage.getItem('RefreshToken');

    if (accessToken && refreshToken) {
        $('#loginOption').html(`
            <div class="text-wrapper login-success">
                <div class="logout">
                    <span id="logOut" class="fs-15 medium">Î°úÍ∑∏ÏïÑÏõÉ</span>
                </div>
                <div class="mypage">
                    <span id="myPage" class="fs-15 medium">ÎßàÏù¥ÌéòÏù¥ÏßÄ</span>
                </div>
            </div>
        `);
    } else {
        $('#loginOption').html(`
            <div class="text-wrapper">
                <div class="login">
                    <span id="login" class="fs-15 medium">Î°úÍ∑∏Ïù∏</span>
                </div>
                <div class="signup">
                    <span id="signUp" class="fs-15 medium">ÌöåÏõêÍ∞ÄÏûÖ</span>
                </div>
            </div>
        `);
    }


    $('input').focus(function () {
        $(this).parent().addClass('focused');
    }).blur(function () {
        $(this).parent().removeClass('focused');
    });


    $('#login').on('click', function() {
        if (true) {
            window.location.href = `/login`;
        } else {
            window.location.href = `/home`
        }
    });


    $('#signUp').on('click', function() {
        console.log('ÌöåÏõêÍ∞ÄÏûÖ ÌÅ¥Î¶≠');
        window.location.href = '/signup';
    });

    $('#logOut').on('click', function() {
        if (accessToken && refreshToken) {
            if (confirm("Î°úÍ∑∏ÏïÑÏõÉ ÌïòÏãúÍ≤†ÏäµÎãàÍπå?")) {
                localStorage.removeItem('Authorization');
                localStorage.removeItem('RefreshToken');
                window.location.href = '/home'; // ÌéòÏù¥ÏßÄ Ïù¥Îèô
            }
        } else {
            alert("ÏûòÎ™ªÎêú Ï†ëÍ∑ºÏûÖÎãàÎã§.");
        }
    });

    $('#myPage').on('click', function() {
        $.ajax({
            url: `http://localhost:8080/users/profile`,
            type: 'GET',
            xhrFields: {
                withCredentials: true // ÌïÑÏöî Ïãú Ï∂îÍ∞Ä
            },
            crossDomain: true,
            headers: {
                'Authorization': 'Bearer ' + accessToken // Ìó§ÎçîÎ™Ö ÏàòÏ†ï
            },
            beforeSend: function (xhr) {
                xhr.setRequestHeader('Authorization', accessToken); // Ìó§ÎçîÎ™Ö ÏàòÏ†ï
            },
            success: function (likeResponseDto) {
                console.log(likeResponseDto)
                window.location.href = '/mypage';
            },
            error: function (jqXHR) {
                const commentResponse = jqXHR.responseJSON;
                const commentResponseText = jqXHR.responseText;
    
                if (commentResponse != null) {
                    alert(commentResponse.message);
                    window.location.href = '/login';
                    localStorage.removeItem('Authorization');
                    localStorage.removeItem('RefreshToken');
                } else {
                    alert(commentResponseText)
                }
    
            }
        });
    });
});